@media (max-width: 1000px) {

    .project-backdrop {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        overflow-x: scroll;
    }

    ::-webkit-scrollbar {
        width: 10px;
    }

    ::-webkit-scrollbar-track {
        background: rgba(133, 115, 115, 0.1);
    }

    ::-webkit-scrollbar-thumb {
        border-radius: 1rem;
        background: rgba(0,0,0,0.25);
    }

}